@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Colors: 

#141b1f, rgb(20, 27, 31) - dark,
rgba(23, 162, 184, 1)- blue
*/

/*------ Layout ------*/

body, html {
  height: 100%;
}

#root {
  font-family: 'Montserrat', 'Arial', sans-serif;
  background-color: #141b1f;
  min-height: 100%;
}



/*------ Link styles ------ */

.link-style-hash {
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
}

.link-style, .link-style:hover, .link-style-hash:hover {
  color: white;
}

.rotate-animation {
  border: none;
}

.rotate-animation:hover {
  -webkit-animation: rotate 1.5s;
          animation: rotate 1.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.underscore, .underscore-sections {
  position: relative;
}

.underscore::after, .underscore-sections::after {
  content: '';
  background-color: rgba(23, 162, 184, 1);
  position: absolute;
  bottom: -0.2rem;
  left: 0;
  display: block;
  width: 100%;
  height: 0.1rem;
  -webkit-transform: scale(0, 1);
          transform: scale(0, 1);
  -webkit-transition: -webkit-transform ease-in-out 250ms;
  transition: -webkit-transform ease-in-out 250ms;
  transition: transform ease-in-out 250ms;
  transition: transform ease-in-out 250ms, -webkit-transform ease-in-out 250ms;
}

.underscore-sections::after {
  height: 0.06rem;
}

.underscore:hover::after, .underscore-sections:hover::after {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }
  10% {
    -webkit-transform: rotateY(30deg);
            transform: rotateY(30deg);
  }
  20% {
    -webkit-transform: rotateY(60deg);
            transform: rotateY(60deg);
  }
  30% {
    -webkit-transform: rotateY(80deg);
            transform: rotateY(80deg);
  }
  40% {
    -webkit-transform: rotateY(120deg);
            transform: rotateY(120deg);
  }
  50% {
    -webkit-transform: rotateY(160deg);
            transform: rotateY(160deg);
  }
  60% {
    -webkit-transform: rotateY(200deg);
            transform: rotateY(200deg);
  }
  70% {
    -webkit-transform: rotateY(240deg);
            transform: rotateY(240deg);
  }
  80% {
    -webkit-transform: rotateY(300deg);
            transform: rotateY(300deg);
  }
  90% {
    -webkit-transform: rotateY(340deg);
            transform: rotateY(340deg);
  }
  100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }
  10% {
    -webkit-transform: rotateY(30deg);
            transform: rotateY(30deg);
  }
  20% {
    -webkit-transform: rotateY(60deg);
            transform: rotateY(60deg);
  }
  30% {
    -webkit-transform: rotateY(80deg);
            transform: rotateY(80deg);
  }
  40% {
    -webkit-transform: rotateY(120deg);
            transform: rotateY(120deg);
  }
  50% {
    -webkit-transform: rotateY(160deg);
            transform: rotateY(160deg);
  }
  60% {
    -webkit-transform: rotateY(200deg);
            transform: rotateY(200deg);
  }
  70% {
    -webkit-transform: rotateY(240deg);
            transform: rotateY(240deg);
  }
  80% {
    -webkit-transform: rotateY(300deg);
            transform: rotateY(300deg);
  }
  90% {
    -webkit-transform: rotateY(340deg);
            transform: rotateY(340deg);
  }
  100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
  }
}

/* ------ Main elemets Titles h1, h2, h3,... ------ */

#home h2, #portfolio h2, #blog h2 {
  background-color: rgba(0, 0, 0, 0.8);
}

#home h2, #blog h2 {
  height: 6.2rem;
}

#portfolio h2 {
  height: 4.2rem;
}

/*------ Navigation------*/

#nav-bar {
  background-color: #141b1f;
}

.navbar-collapse {
  background-color: #141b1f;
}

svg.active-item {
  color: rgba(23, 162, 184, 1);
}

.active-item::before {
  content: '';
  background-color: rgba(23, 162, 184, 1);
  position: absolute;
  top: -0.3rem;
  left: 45%;
  display: block;
  width: 0.35rem;
  height: 0.32rem;
  border-radius: 50%;
}

div.breadcrumb {
  background-color: rgba(0, 0, 0, 0.7);
  margin: 0;
  height: 100%;
}

span.active.breadcrumb-item::before {
  color: white;
}

#hash-list-toggler {
  background-color: #141b1f;
  border: 0.125rem dotted gray;
  width: 2.5rem;
  height: 2.5rem;
}

/*------ Social icons ------ */

.social-icon-item {
  font-size: 1.9rem;
}

/*------ Section ------*/

/*------ Home page ------ */

#home .h2-row {
  height: 6.25rem;
  background-attachment: fixed;
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
}

/*---- Hero Image section ----*/

#hero-image {
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.4)), to(rgba(0, 0, 0, 0.4))), url(/my-website/static/media/hero_image.f0fb1496.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(/my-website/static/media/hero_image.f0fb1496.jpg);
  height: calc(100vh - 4.5rem);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

/*------ About section ------*/

#about .h2-row {
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.3)), to(rgba(0, 0, 0, 0.3))), url(/my-website/static/media/about_parallax_bw.83549916.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(/my-website/static/media/about_parallax_bw.83549916.jpg);
}

.about-icon {
  width: 6.25rem;
  height: 6.25rem;
  font-size: 3.125rem;
  background-color: rgba(23, 162, 184, 1);
}

.about-text {
  font-size: 0.875rem;
}

/*------ Skills ------*/

#skills .h2-row {
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.3)), to(rgba(0, 0, 0, 0.3))), url(/my-website/static/media/skills_parallax_bw.6f6ad9a2.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(/my-website/static/media/skills_parallax_bw.6f6ad9a2.jpg);
}

.skills-icon-item svg {
  font-size: 3.125rem;
}

.skills-icon-item {
  background-color: rgba(20, 27, 31, 0.95);
  color: white;
  height: 7.5rem;
  width: 7.5rem;
}

#skills .bg-section {
  background-color: rgba(23, 162, 184, 1);
}

#github .bg-section {
  background-attachment: fixed;
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.3)), to(rgba(0, 0, 0, 0.3))), url(/my-website/static/media/gh-projects-parallax-bw.a5114bb8.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(/my-website/static/media/gh-projects-parallax-bw.a5114bb8.jpg);
}

/*------ Current Projects ------*/

#current-projects .h2-row {
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.3)), to(rgba(0, 0, 0, 0.3))), url(/my-website/static/media/current_p_parallax_bw.8d91a0a3.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(/my-website/static/media/current_p_parallax_bw.8d91a0a3.jpg);
}

.current-p-card {
  background-color: rgba(20, 27, 31, 0.95);
}

/*------ Footer ------*/

#footer-page-bottom {
  background-color: #141b1f;
}

/*------- Portfolio ------*/

#portfolio .h2-row {
  background-color: rgba(23, 162, 184, 1);
}

.card-footer.gh-card-bg, .card-header.gh-card-bg, .cp-projects-links-bg {
  background-color: rgb(20, 27, 31);
}

.card.gh-project {
  border: none;
}

/*--- Statistics ---*/

.statistica {
  background-color: rgba(20, 27, 31, 1);
  width: 12.5rem;
  height: 12.5rem;
  border: 0.5rem solid rgba(23, 162, 184, 1);
}

.statistica svg {
  font-size: 2rem;
}

.stat-name {
  font-size: 0.875rem;
}

.stat-points {
  font-size: 1.125rem;
}

/*------ Blog ------*/

.card-footer.art-card-bg, .card-header.art-card-bg {
  background-color: rgb(20, 27, 31);
}

.card.article-preview-card {
  border: 0.3rem solid rgb(20, 27, 31);
}

#articles .h2-row {
  background-attachment: fixed;
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.3)), to(rgba(0, 0, 0, 0.3))), url(/my-website/static/media/blog-parallax-bw.b37ac136.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(/my-website/static/media/blog-parallax-bw.b37ac136.jpg);
}
code {
  display: block;
  white-space: pre-wrap;
}
.code-styles, .useful-info {
  font-size: 0.625rem;
}
.page-screenshot{
  cursor: pointer;
}

blockquote p {
